@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
    border: none;
    outline: none;
    scroll-behavior: smooth;
    font-family: 'Poppins', sans-serif;
}
:root{
    --bg-color:#000000;
    --second-bg-color:#161616;
    --text-color:#fff;
    --main-color:#7b4bb7;
}
html{
    font-size: 62.5%;
    overflow-x:hidden ;
}
body{
    background-color: var(--bg-color);
    font-family: 'Poppins', sans-serif;
    color: var(--text-color);
}
html::-webkit-scrollbar{
    width: 0.8rem;
}
html::-webkit-scrollbar-track{
    background-color: var(--bg-color);
}
html::-webkit-scrollbar-thumb{
    background-color: var(--main-color);
};
section{
    min-height: 100vh ;
    padding: 5rem 9% 5rem;
}
.home{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 8rem;
    background-color: var(--bg-color);
    opacity: 0;
    transform: translateY(30px);
    animation: moveup 0.5s linear forwards;
}
@keyframes moveup{
    100%{
        opacity: 1;
    }
}

.home .home-content h1{
    font-size: 6rem;
    font-weight: 700;
    line-height: 1.3;
}
span{
    color: var(--main-color);
}
.home-content h3{
    font-size: 4rem;
    margin-bottom: 1rem;
    font-weight: 700;
}
.home-img{
    border-radius: 50%;
}
.home-img img{
    position: relative;
    width: 32vw;
    border-radius: 50%;
    box-shadow: 0 0 25px var(--main-color);
    cursor: pointer;
}
.home-img img:hover{
    transform: scale(1.01);
    filter: drop-shadow(0 0 25px var(--main-color));


}
.home-content p{
    font-size: 1.8rem;
    font-weight: 500;
}
.socail-icon a{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 4rem;
    height: 4rem;
    background-color: transparent;
    font-size: 2rem;
    color: var(--main-color);
    margin: 3rem 1.5rem 3rem 0;
    transition: 0.3s ease;
}
.socail-icon a:hover{
    color: whitesmoke;
    transform: scale(1.3)translateY(-5px);
}
.btn{
    display: inline-block;
    padding: 1rem 2.8rem;
    background: var(--bg-color);
    border-radius: 4rem;
    font-size: 1.6rem;
    color: var(--main-color);
    border: 2px solid var(--main-color);
    letter-spacing: 0.1rem;
    font-weight: 600;
    transition: 0.3s ease;
    cursor: pointer;
}
.img1{
    width: 25px;
}

.btn:hover{
    transform:scale(1.03);
    background-color: var(--main-color);
    color: black;
    box-shadow: 0 0 25px var(--main-color);
}
.typing-text{
    font-size: 34px;
    font-weight: 600;
    min-width: 280px;
}
.typing-text span{
    position: relative;
}
.typing-text span::before{
    content: "Frontend Developer";
    color: var(--main-color);
    animation: words 20s infinite;
}
.typing-text span::after{
    content: '';
    background: var(--bg-color);
    position: absolute;
    width: calc(100% + 8px);
    height: 100%;
    border-left: 3px solid var(--bg-color);
    right: -8;
    animation: cursor 0.6s infinite,typing 23s steps(0) infinite;
}
@keyframes words{
    0%, 20%{
        content:'Web Developer';
    }
    
   21%, 40%{
        content:'UI/UX Developer';
    }
    21%, 40%{
        content:'Computer Trainer';
    }
    61%, 80%{
        content:'Graphic Designer';
    }
    81%, 100%{
        content:'Video Editing';
    }
}
@keyframes typing{
    10%, 15%,
    30%, 35%,
    50%, 55%,
    70%, 75%,
    90%, 95%{
        width: 0;
    }
    5%, 20%,
    25%, 40%,
    45%, 60%,
    65%, 80%, 85%{
        width: calc(100% + 8px);
    }
}

@media (max-width:1000px) {
    .home{
        gap: 4rem;
    }
}

@media (max-width:995px) {
    .home{
        flex-direction: column;
        margin: 5rem 4rem;
    }
    .home .home-content h3{
        font-size: 3rem;
    }
    
    .home-content h1{
        font-size: 5rem;
    }
    .home-img img{
        width: 70vw;
        margin-top: 4rem;
    }
}
.services{
    background: var(--second-bg-color);
 
}
.services h2{
    font-size: 3rem;
    border-bottom: 1px solid var(--main-color) ;
}
.services-container{
    display: grid;
    grid-template-columns:repeat(3, 1fr);
    align-items: center;
    gap: 2.5rem;
    margin-top: 25px;
}
.services-box{
    background-color: transparent;
    height:300px;
    border-radius: 2rem;
    cursor: pointer;
    transition: 0.3s ease; 
    font-size: 5px; 
}

.services-box:hover{
    background-color: var(--main-color);
    color: var(--text-color);
    transform: scale(1.03);
    border: 1px solid var(--main-color);
}
.services-box .services-info{
    display: flex;
    flex-direction: column;
    text-align: center;
    max-height: 200px;
    justify-content: center;
    align-items: center;
    padding: 5em;
}
.services-info h4{
    font-size: 2.5rem;
    font-weight: 500;
    margin-bottom:1rem;
}
.services-info p{
    font-size: 1.6rem;
    font-weight: 400;
    max-height: 100px;
    margin:auto ;
}
 @media (max-width:991){
    .section{
        padding: 10rem 3% 2rem;
    }

    .services{
        padding-bottom: 10rem 3% 2rem;
    }
    .services-box{
        margin-top: 50px;
     
    };
 
};
 @media (max-width:825px){
    .section h2{
        margin-bottom:3rem ;
    }
    .services-container{
        grid-template-columns: repeat(1, 1fr);
        margin-top: 25px;
    }
    .services-box{
        margin-top: 50px;
        font-size: 5px;
        
    };
    
};

.skills{
    background: var(--bg-color);
}
.skills .heading{
    font-size: 3rem;
    border-bottom: 1px solid var(--main-color) ;
}
.skills .container{
    background: #2c2c2c54;
    color:var(--text-color) ;
    border-radius: 1rem;
    padding: 2rem;
    width: 70%;
    margin: auto;
    margin-top: 2rem;   
}
.skills .container .row{
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    flex-direction: wrap;
    gap: 1.8rem;
}
.skills .container .bar{
    margin-bottom:15px ;
    padding: 10px;
    border-radius: 1rem;
    background: var(--bg-color);
    transition: 0.3s ease;
}
.skills .container .bar:hover{
    box-shadow: 0 4px 10px var(--main-color);
}
.skills .container .bar .info img{
    width: 50px;
    height: 50px;
}
.skills .container .bar .info{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    margin-top: 1rem ;
}
.skills .container .bar .info span{
 font-size: 2rem;
 font-weight: 500;
 margin-left:0.5rem ;
}
@media screen and (max-whidth:600px) {
    .skils .container{
        padding: 0;
        margin: 0;
    }
 
    .skills .container .row{
        grid-template-columns: repeat(2, 1fr);
        margin: 1rem;
        padding: 2rem 0.2rem 2rem 0.2rem;
        gap: 1rem;
    }
    .skills .container{
        margin-top: 5px;
        width: 100%;
    }
    
}


.footer{
    position: relative;
    bottom: 0;
    width: 100%;
}

.footer .copyright{
    margin-top: 50px;
    text-align: center;
    font-size: 16px;
    color: #474747;
}
@media(max-width:1285px){
    html{
        font-size: 55%;
    }
    .services-container{
        padding-bottom:7rem;
        grid-template-columns: repeat(1, 1fr);
        margin: 0 5rem;
    }
}
@media(max-width:991px){
    .header{
        padding: 2rem 3%;
    }
    .section{
        padding: 10rem 3% 2rem;
    }
    .services{
        padding-bottom:7rem;
    }
    .footer{
        padding: 2rem 3%;
    }
    .skills .container .row{
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        flex-direction: wrap;
        gap: 1.8rem;
    }
    .eduction .content{
        background: var(--main-color);
        position: relative;
        border-radius: 6px;
        width: 150px;
    }
    .eduction .left{
        left: -60px;
    }
    .eduction .right{
        left: 160px;
    }
    .eduction .container::after{
        right: -75px;

    };
  
  
};
@media(max-width:895px){
    .services h2{
        margin-bottom: 3rem;
    }
    .services-container{
      grid-template-columns: repeat(1,1fr);
    }

}

.contact h2{
    margin-bottom:3rem;
    color: whitesmoke;
}
.contact .heading{
    text-align: center;
    font-size: 5rem;
}
.contact form{
    max-width: 70rem;
    margin: 1rem auto;
    margin-bottom:3rem;
    text-align: center;
}
.contact form .input-box{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}
.contact form .input-box input, .contact form textarea{
    width: 100%;
    padding: 1.5rem;
    font-size: 1.6rem;
    color: var(--text-color);
    background-color: var(--bg-color);
    border-radius: 0.8re;
    border: 2px solid var(--main-color);
    margin: 1rem 0;
    resize: none;
}
.contact form .btn{
    margin-top: 2rem;
}